.custom-button {
    background-color: #091D9E;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 14px;
    transition: background-color 0.3s ease;
  }

  .custom-button:hover {
    background-color: #0A25C5;
  }

  .custom-button:active {
    background-color: #08188B;
  }

  .cart-icon {
    width: 20px;
    height: 20px;
    fill: white;
  }
