/* Category.css */
.category-wrapper {
    padding: 40px 20px;
  }

  .category-title {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 20px;
  }

  .category-container {
    display: flex;
    gap: 20px;
    overflow-x: auto;
    padding-bottom: 10px;
  }

  .category-card {
    flex: 0 0 auto;
    width: 150px;
    text-align: center;
    text-decoration: none;
    color: black;
    background: #f8f8f8;
    border-radius: 10px;
    padding: 10px;
    transition: 0.3s;
  }

  .category-card:hover {
    transform: scale(1.05);
  }

  .category-card img {
    width: 100%;
    height: 120px;
    object-fit: contain;
  }

  .category-card p {
    margin-top: 10px;
    font-size: 16px;
  }
