.footer-background {
    background-color: #BCC5FF;
    padding: 20px 0;
    width: 100%;
    box-sizing: border-box;
}

.footer-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 20px;
    box-sizing: border-box;
    width: 100%;
}

.footer-section {
    flex: 1;
    margin-right: 20px;
}

.footer-section:last-child {
    margin-right: 0;
}

.footer-logo-section {
    flex: 2; }

.logo-text {
    font-size: 24px;
    font-weight: bold;
    color: #6a0dad;
}

.footer-description, .footer-quote {
    margin: 10px 0;
    font-size: 14px;
    color: #6c757d;
}

.footer-section-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
}

.footer-link a {
    text-decoration: none;
    color: #007bff;
    display: block;
    margin-bottom: 5px;
}

.footer-link a:hover {
    text-decoration: underline;
}

.footer-logos {
    display: flex;
    margin-top: 10px;
}

.payment-logo {
    margin-right: 10px;
    width: 50px;
}

.footer-copyring {
    margin-top: 20px;
    font-size: 14px;
    color: #6c757d;
    text-align: center;
}
