/* ProductCard.css */
.product-card {
  background: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0px 8px 32px rgba(0, 0, 0, 0.12);
}

.product-image-wrapper {
  padding: 24px;
  background: #F8F9FA;
  position: relative;
}

.product-image {
  width: 100%;
  height: 240px;
  object-fit: contain;
  mix-blend-mode: multiply;
}

.discount-badge {
  position: absolute;
  top: 16px;
  right: 16px;
  background: #FF4444;
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
}

.product-content {
  padding: 24px;
}

.product-title {
  font-size: 18px;
  line-height: 1.4;
  color: #1A1A1A;
  margin-bottom: 16px;
  min-height: 72px;
}

.price-wrapper {
  display: flex;
  gap: 12px;
  align-items: baseline;
  margin-bottom: 24px;
}

.old-price {
  color: #909090;
  font-size: 14px;
  text-decoration: line-through;
}

.new-price {
  color: #D32F2F;
  font-size: 20px;
  font-weight: 700;
}

.availability-badge {
  display: inline-block;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 14px;
}

.in-stock {
  background: #E8F5E9;
  color: #2E7D32;
}

.out-of-stock {
  background: #FFEBEE;
  color: #C62828;
}

.cart-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 100%;
  padding: 12px;
  background-color: #2196F3;
  color: white;
  border: none;
  border-radius: 28px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.cart-button:hover {
  background-color: #1976D2;
}

.cart-icon {
  width: 20px;
  height: 20px;
  fill: white;
}
