/* Filters.css */

.filters-panel {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
  margin-bottom: 30px;
  position: sticky;
  top: 20px;
  z-index: 10;
}

.filters-panel h3 {
  font-size: 20px;
  color: #1a1a1a;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
  font-weight: 600;
}

.filter-group {
  margin-bottom: 22px;
}

.filter-group label {
  display: block;
  font-size: 14px;
  color: #666;
  margin-bottom: 12px;
  font-weight: 500;
}

/* Стили для селектов */
.filter-group select {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  background-color: #fff;
  font-size: 14px;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6 9 12 15 18 9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 18px;
  transition: all 0.3s ease;
}

.filter-group select:focus {
  outline: none;
  border-color: #0071e3;
  box-shadow: 0 0 0 3px rgba(0, 113, 227, 0.1);
}

/* Стили для полей ввода цены */
.price-inputs {
  display: grid;
  grid-template-columns: 1fr 24px 1fr;
  gap: 10px;
  align-items: center;
}

.price-inputs input {
  width: 100%;
  padding: 12px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.3s ease;
}

.price-inputs input:focus {
  border-color: #0071e3;
  box-shadow: 0 0 0 3px rgba(0, 113, 227, 0.1);
  outline: none;
}

.price-inputs span {
  text-align: center;
  color: #888;
  font-weight: 500;
}

/* Стили для ошибок */
.price-error {
  color: #d32f2f;
  font-size: 12px;
  margin-top: 8px;
  display: none;
}

.price-inputs:has(input:invalid) + .price-error {
  display: block;
}

/* Адаптивность */
@media (max-width: 768px) {
  .filters-panel {
    position: static;
    margin: 0 -15px 20px;
    border-radius: 0;
    box-shadow: none;
    border-bottom: 1px solid #eee;
  }

  .price-inputs {
    grid-template-columns: 1fr;
  }

  .price-inputs span {
    display: none;
  }
}

/* Анимации */
@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.filters-panel {
  animation: slideDown 0.3s ease-out;
}
