.product-list-container {
  padding: 40px 20px;
}

.product-list-title {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 30px;
}

.product-list {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding-bottom: 10px;
}

.product-list::-webkit-scrollbar {
  height: 8px;
}

.product-list::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 8px;
}

.product-list::-webkit-scrollbar-thumb:hover {
  background: #555;
}
